/* My CSS */
body{
  background: #282c34;
}

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
  'Lucida Sans', Arial, sans-serif;
}

.App{
  height: auto;
  margin: 128px auto;
  width: 520px;
  min-height: 600px;
  background: #61dafb;
  display: flex;
  text-align: center;
  /* justify-content: start; */
  flex-direction: column;
  color: #000;
  border-radius: 15px;
  padding-bottom: 32px;
}

h1{
  margin: 32px 0;
  color: crimson;
  font-size: 24px;
}

.complete{
  text-decoration: line-through;
  opacity: 0.5;
}

.todo-form{
  margin-bottom: 32px;
}

.todo-input{
  padding: 14px 32px 14px 16px;
  border-radius: 4px 0 0 4px;
  border: 2px solid #282c34;
  outline: none;
  width: 75%;
  background: transparent;
  color: #000;
}

.todo-input::placeholder{
  color: #282c34;
}

.todo-button{
  padding: 16px;
  border: none;
  border-radius: 0 4px 4px 0;
  cursor: pointer;
  outline: none;
  background: #282c34;
  color: aliceblue;
  text-transform: capitalize;
}

.todo-input.edit{
  border: 2px solid tomato;
}

.todo-button.edit{
  background: tomato;
}

.todo-container{
  display: flex;
  flex-direction: row;
  position: relative;
}

.todo-row{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 4px auto;
  color: #fff;
  background: coral;
  padding: 16px;
  border-radius: 5px;
  width: 90%;
}

.icons{
  display: flex;
  align-items: center;
  font-size: 24px;
  cursor: pointer;
}

.delete-icon{
  margin-right: 5px;
  color: #fff;
}

.edit-icon{
  margin-right: 5px;
  color: #fff;
}

